import React, { Component } from 'react';

class BusInfo extends Component {
    getBusInfo = async () => {
        const rawBusData = await fetch('https://www.metlink.org.nz/api/v1/StopDepartures/7021')
        const busData = await rawBusData.json()
        console.log(busData)
    }

  render() {
      this.getBusInfo()
    return (
        <div>Bus shizzzz</div>
    );
  }
}

export default BusInfo;



